<template>
    <div>
        <h3>Login.vue</h3>
        <el-card shadow="always">
            <el-form :inline="true" >
                <el-form-item  label="" label-width="80px">
                    <el-input v-model="state.userid" ref="userid" size="mini" placeholder=""/>
                </el-form-item>
            </el-form>
            <el-form :inline="true" >
                <el-form-item label="" label-width="80px" style="margin-top:-20px">
                    <el-input v-model="state.userpw" ref="userpw" size="mini" type="password" placeholder="" @keydown.enter.prevent="handleLogin"/>
                </el-form-item>
            </el-form>
            <el-button type="primary" size="mini" round style="margin-left:75px" @click="handleLogin"></el-button>
            <el-button type="primary" size="mini" round @click="handleJoin" ></el-button>
            </el-card>

        

    </div>
</template>

<script>
import { reactive, ref } from 'vue';
import {useRouter} from 'vue-router';
import axios from 'axios';
import {useStore} from 'vuex';
export default {
    

    //ver 3.0
    setup () { // this   

        const store = useStore();
        const router = useRouter();

        const state = reactive({
            userid : '',
            userpw : '',

        });

        const userid = ref(null);
        const userpw = ref(null);

        const handleLogin = async() =>{
            console.log(' ', state.userid, state.userpw);
            if(state.userid===''){
                alert(' .')
                userid.value.focus();
                return false;
            }
            if(state.userpw===''){
                alert(' .')
                userid.value.focus();
                return false;
            }

            const url = `/member/select`;
            const headers = {"Content-Type":"application/json"};
            const body = {email : state.userid, password : state.userpw};
            const response = await axios.post(url, body, {headers});
            if(response.data.status === 200){
                console.log(response.data.token);
                //  ()
                sessionStorage.setItem("TOKEN", response.data.token);
                alert(' ');

                // actions  store/state .
                store.dispatch("handleData", {});
              
                const curl = sessionStorage.getItem("CURL");
                if(curl === null){

                    //  
                    router.push({name:'Home'});

                    // App.vue    
                    store.commit("setMenu", "/");
                    // 
                    store.commit("setLogged", true);
                }
                else{ //   
                    const query = JSON.parse(sessionStorage.getItem("CURL_QUERY"));
                    const params = JSON.parse(sessionStorage.getItem("CURL_PARAMS"));
                    router.push({name:curl, query:query, params:params});
                }
            }
        };

        const handleJoin = () => {
            router.push({name:'Join'});
            
        }

        return {state, handleLogin, handleJoin, userid, userpw}  // return 
    },

        //ver 2.0
    // data(){
    //     return{
    //         state : {
    //             userid : 'aaa',
    //             userpw : 'bbb'
    //         }
    //     }
    // },


}
</script>

<style lang="scss" scoped>

</style>