<template>
    <div>
        <h3>src/components/BoardContent.vue</h3>

        <div v-if="state.item">
            <el-card>
                <el-descriptions direction="horizontal" :column="4"  border>
                    <el-descriptions-item label="" :span="2">{{state.item.title}}</el-descriptions-item>
                    <el-descriptions-item label="" :span="2">{{state.item.regdate}}</el-descriptions-item>
                    <el-descriptions-item label="" :span="2">{{state.item.writer}}</el-descriptions-item>
                    <el-descriptions-item label="" :span="2">{{state.item.hit}}</el-descriptions-item>
                    <el-descriptions-item label="">
                    <img :src="state.item.imageurl" style="width:300px" />
                    </el-descriptions-item>
                </el-descriptions>
            </el-card>
            <hr />

            <router-link to="/board"><button></button></router-link>
            <button @click="handleDelete"></button>
            <button @click="handleUpdate"></button>
            <button v-if="state.item.prev" @click="handleData(1)"></button>
            <button v-if="state.item.next" @click="handleData(2)"></button>
            <hr />

            <div v-for="tmp in state.reply" :key="tmp" >
                <table border="1" class="one" style="width:500px" >  
                    <th>{{tmp._id}}</th>
                    <th ></th>
                    <td>{{tmp.content}}</td>
                    <th></th>
                    <td>{{tmp.writer}}</td>     
                    <button @click="handleReplyDelete(tmp._id)"></button>         
                </table>
            </div>
            <hr />
            <el-form :inline="true">
                    <el-form-item label="" >
                        <el-input :span="10" v-model="state.reply1.content" placeholder=""/>
                    </el-form-item>
                    <el-form-item label="" >
                        <el-input v-model="state.reply1.writer" placeholder=""/>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" round @click="handleReplyAction"></el-button>
                    </el-form-item>
            </el-form>
        </div>

    </div>
</template>

<script>
import axios from 'axios'; //
import { reactive, onMounted } from 'vue'; //state,
import { useRoute } from 'vue-router'; //  route.query
import { useRouter } from 'vue-router'; // 

export default {
    setup () {
        const route = useRoute();
        const router = useRouter();

        // state 
        const state = reactive({
            no : route.query.no, //?no=113
            reply1 : {
                content : '',
                writer : '',
            }
        });

        const handleReplyDelete = async(no) => {
            const url = `/board/deletereply?no=${no}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.delete(url, {headers});
            console.log(response.data);
            if(response.data.status===200){
                alert('');
                await handleReplyMound(state.no);
            }


        }


        //  
        const handleDelete = async() => {
            if(confirm('?')){
                console.log('handleDelete');
                const url = `/board/delete?no=${state.no}`;
                const headers = {"Content-Type":"application/json"};
                const response = await axios.delete(url, {headers});
                console.log(response.data);
                if(response.data.status===200){
                    alert('');
                    router.push({name:"Board", query:{page:1, text:''}})
                }
            }
        };

        const handleMount = async(no) => {
            const url = `board/selectone?no=${no}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.get(url, {headers});
            console.log(response.data);
            if(response.data.status === 200){
                state.item = response.data.result;
            }
        }

        const handleReplyMound = async(no) => {
            const url = `board/selectreply?no=${no}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.get(url, {headers});
            if(response.data.status===200){
                state.reply = response.data.result;
            }
        }
        const handleReplyAction = async() => {
            const url = `/board/insertreply`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                content : state.reply1.content,
                writer : state.reply1.writer,
                boardno : state.no
            }
            const response = await axios.post(url, body, {headers});
            console.log(response.data);
            if(response.data.status === 200){
                await handleReplyMound(state.no);
            }
        }

        onMounted( async() => {
            await handleMount(state.no);
            await handleReplyMound(state.no);

        });

        //     
        const handleUpdate = () => {
            router.push({name:"BoardUpdate", query:{no : state.no}});
        }

        const handleData = async(idx) => {
            if(idx === 1){//
                //  
                //  ->  onMounted()
                router.push({name:"BoardContent", query:{no:state.item.prev}});
                state.no = state.item.prev;
                await handleMount(state.no);
                await handleReplyMound(state.no);
            }
            else if(idx === 2){//
                router.push({name:"BoardContent", query:{no:state.item.next}});
                state.no = state.item.next;
                await handleMount(state.no);
                await handleReplyMound(state.no);
                
            }
        }

        return{state, handleDelete, handleUpdate, handleReplyAction, handleData, handleReplyDelete}

    }, 

    
}
</script>

<style lang="scss" scoped>
.one{
    text-align:center;
    border-collapse: collapse;
    table-layout: fixed;
    border-style: solid;
    
   
}

</style>