<template>
    <div class="style1">
        <el-card shadow="always">
            <el-form :inline="true"  >
                <el-form-item  label="" label-width="80px">
                    <el-input  size="mini" ref="userid" v-model="state.userid" placeholder="" @keyup="handleEmailCheck"/>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" size="mini" round >{{state.usermailcheck}}</el-button> 
                </el-form-item>
            </el-form>
            
            <el-form :inline="true"  >
                <el-form-item label="" label-width="80px" style="margin-top:-20px" >
                    <el-input  size="mini" ref="userpw" v-model="state.userpw" type="password" placeholder=""/>
                </el-form-item>
            </el-form>

            <el-form :inline="true"  >
                <el-form-item label="" label-width="80px" style="margin-top:-20px">
                    <el-input  size="mini" ref="userpw1" v-model="state.userpw1" type="password" placeholder=""/>
                </el-form-item>
            </el-form>

             <el-form :inline="true"  >
                <el-form-item  label="" label-width="80px" style="margin-top:-20px">
                    <el-input  size="mini" ref="username"  v-model="state.username" placeholder="" @keyup.enter="handleJoin"/>
                </el-form-item>
            </el-form>


            <el-form :inline="true" style="margin-top:-20px" >
                <el-form-item label="" label-width="80px">
                    <el-input  type="textarea" v-model="state.text" placeholder=""></el-input>
                </el-form-item>
            </el-form>

            <el-form :inline="true"  >
                <el-form-item  label="" label-width="80px" style="margin-top:-20px">
                    <el-select v-model="state.userrole" size="mini" placeholder="Select">
                        <el-option value="CUSTOMER" label=""></el-option>
                        <el-option value="SELLER" label=""></el-option>
                    </el-select>
              </el-form-item>
            </el-form>
            <!--  : 
            <select v-model="state.userrole">
                <option value="CUSTOMER" ></option>
                <option value="SELLER" ></option>
            </select><br /> -->

            <el-button type="primary" size="mini" round style="margin-left:75px" @click="handleJoin"></el-button>
            <el-button type="primary" size="mini" round @click="hendleHome"></el-button>
        </el-card>

       
    </div>
</template>

<script>
import axios from 'axios'
import { reactive, ref } from 'vue'
import {useRouter} from 'vue-router';
import {useStore} from 'vuex';
export default {
    setup () {
        // High   :  
        //  string   
        //  1  2  3

        const store = useStore();

        
        
        const state = reactive({
            userid     : '',
            userpw     : '',
            userpw1    : '',
            username   : '',
            usermailcheck : '',
            text       : '',
            userrole : 'CUSTOMER',

        });

        const router = useRouter();


        //Low    :  
        const userid = ref(null); //  aaa  
        const userpw = ref(null);
        const userpw1= ref(null);
        const username = ref(null);

        //   
        const validEmail = (email) => {
            // 
            var re = /[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]*$/i;
            return re.test(email);
        }

        const handleEmailCheck = async() => {
            if(validEmail(state.userid)){
                console.log(state.userid);
                const url = `/member/emailcheck?email=${state.userid}`;
                const headers = {"Content-Type":"application/json"};
                const response = await axios.get(url, {headers});
                console.log(response.data);
                if(response.data.status === 200){
                    if(response.data.result === 1){
                        state.usermailcheck = '';
                    }
                    else{
                    state.usermailcheck = '';
                    }
                }
            }
             else{
            state.usermailcheck = '';
            }
        }
       


        const handleJoin = async() => {
            if(state.userid===''){
                alert(' .')
                userid.value.focus();
                return false;
            }
            if(state.userpw===''){
                alert(' .')
                 userpw.value.focus();
                return false;
            }
            if(state.userpw1===''){
                alert(' .')
                 userpw1.value.focus();
                return false;
            }
            if(state.userpw1 !== state.userpw){
                alert('  .')
                 userpw1.value.focus();
                return false;
            }
            if(state.username===''){
                alert(' .')
                 username.value.focus();
                return false;
            }

            if(state.usermailcheck !== ''){
                alert('.');
                userid.value.focus();
                return false;
            }

            //    

            const url = `/member/insert`;
            const headers = {"Content-Type":"application/json"};
            const body = {  email    : state.userid, 
                            password : state.userpw, 
                            name     : state.username,
                            role     : state.userrole
                            };
            const response = await axios.post(url, body, {headers});
            console.log(response.data);
            if(response.data.status === 200){
                alert('');
                router.push({name : 'Home'});

                store.commit("setMenu", "/");
                store.commit("setLogged", false);

            }
            
        }

        const hendleHome = () => {
            router.push({name : 'Home'})
        };


11

        return {state, handleEmailCheck, validEmail, username, handleJoin, userid, userpw, userpw1, hendleHome, router }
    }
}
</script>

<!--scss, less => css-->
